  loadData(){
    this.utilsService.loadingStart();
      let tableDataTemp = [];
      for (let index = 0; index < this.utilsService.listProductKho.length; index++) {
        const element = this.utilsService.listProductKho[index];        
        tableDataTemp.push(element);
      }  
      this.lstProduct = tableDataTemp;
      this.utilsService.loadingStop();    
  }

