  filterContent(ev){
    this.lstProduct = [];
    if(ev != "" && ev.length > 2){
      this.lstProduct = this.utilsService.listProductKho.filter((item) => {
        return this.utilsService.xoa_dau(item["Tên sản phẩm"]).toLowerCase().includes(this.utilsService.xoa_dau(ev).toLowerCase());    
      });
    }
    else{
      for (let index = 0; index < this.utilsService.listProductKho.length; index++) {
        const element = this.utilsService.listProductKho[index];
        this.lstProduct.push(element);
      }  
    }    
  }
  
  showDetail(data: any) {
    this.showModalEditProduct(data.ev, data.row._row.data);
  }
  showAdd(ev) {
    this.showModalEditProduct(ev, {});
  }
  async showModalEditProduct(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditDetailProductComponent,
      id: "editDetailsProductModal",
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row },
      cssClass: "edit-detail-product-popover"
    });
    return await popover.present();
  }
